.gallery {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

img {
  width: 250px;
  display: block;
  height: 160px;
}
.stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
}
